export default {
	data() {
		return {
			type: 0, //类别
			loadMoreText: '加载中...',
			showLoadMore: false,
			isMore: true,
			PageNo: 1,
			PageSize: 10,
			mainItem: [],
			typeItem: [{
				id: 0,
				name: '普通用户'
			}, {
				id: 1,
				name: '青铜分店'
			}, {
				id: 2,
				name: '白银分店'
			}, {
				id: 3,
				name: '黄金分店'
			}, {
				id: 4,
				name: '分公司'
			}],
			keyWords: '' //搜索关键词
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		this.initData();
	},
	onUnload() {
		(this.PageNo = 1), (this.mainItem = []), (this.loadMoreText = '加载更多'), (this.showLoadMore = false);
	},
	onReachBottom() {
		//下拉加载
		this.showLoadMore = true;
		setTimeout(() => {
			this.setListData();
		}, 300);
	},
	onPullDownRefresh() {
		//上拉刷新
		this.initData();
	},
	methods: {
		search(res) {
			this.keyWords = res.value;
			this.initData();
		},
		dumpInfo(id) {
			uni.navigateTo({
				url: '/pages/shop/info?id=' + id
			});
		},
		initData() {
			//刷新数据
			setTimeout(() => {
				this.PageNo = 1;
				this.mainItem = [];

				this.getPage();
				uni.stopPullDownRefresh();
			}, 300);
		},
		setListData() {
			//加载更多数据
			this.PageNo += 1;
			if (this.isMore) this.getPage();
		},
		getPage() {
			var _this = this;
			uni.showLoading({
				title: '数据查询中'
			});
			_this.$request.user
				.friends({
					page: _this.PageNo
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						_this.mainItem = _this.mainItem.concat(data.data);
						if (data.data.length < _this.PageSize) {
							this.loadMoreText = '没有更多数据了';
							_this.isMore = false;
						} else {
							_this.showLoadMore = false;
							_this.isMore = true;
						}
					} else {
						_this.showLoadMore = false;
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					_this.showLoadMore = false;
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		}
	}
};
